<template>
    <v-text-field class="full-size"
      :placeholder="getValue(element, 'Placeholder')" 
      :value="getValue(element, 'Value')">
    </v-text-field>
</template>

<script>
import {
  ElementModel,
  PropertyModel,
  StateModel,
  Types
} from "../ElementModel.vue";
import { getValue, setValue } from "../store";

export default {
  name: "input-element",
  props: ["element"],
  created() {
    this.$store.commit("addProperties", {
      element: this.element,
      properties: [
        new PropertyModel("Value", "something"),
        new PropertyModel("Placeholder", "Type something")
      ]
    });
    this.setValue("Width", "auto");
    this.setValue("Height", "auto");
  },
  methods: {
    getValue,
    setValue
  }
};
</script>

<style scoped>

</style>



